<template>
  <div>
    <div v-if="displayPosts.length" class="row mx-0">
      <div
        v-for="post of displayPosts"
        :key="post.id"
        class="col-lg-6 col-sm-12 col-md-12 col-12 post-info"
      >
        <router-link :to="'/post/' + post.id">
          <div class="img-block">
            <img
              v-if="post.images"
              :src="post.images[0]"
              :alt="post.title"
              class="post-image"
            />
            <img
              v-else
              src="https://firebasestorage.googleapis.com/v0/b/vue-realty.appspot.com/o/80land_1.png?alt=media&token=fe18755c-f94a-494f-836d-3408052e8e03"
              :alt="post.title"
              class="post-image"
            />
          </div>
          <div class="row mb-3">
            <div class="col-lg-7 col-md-7 col-sm-12 pr-0">
              <span class="post-title">{{ post.title }}</span>
            </div>
            <div
              v-if="post.price"
              class="col-lg-5 col-md-5 col-sm-12 text-lg-right text-md-right text-sm-left text-left pl-md-0 pl-lg-0"
            >
              <span class="post-price">{{
                Number(post.price).toLocaleString() + ' тг'
              }}</span>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-1 col-md-1 col-sm-1 col-1 pb-2">
              <i class="icon location"></i>
            </div>
            <div
              class="col-lg-10 col-md-10 col-sm-10 col-10 pb-2"
              style="display: flex; align-items: center;"
            >
              <span v-if="post.location" class="post-location">
                {{ post.location }}
              </span>
              <span v-else class="post-location">Адрес не указан.</span>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-1 col-md-1 col-sm-1 col-1 pb-2">
              <i class="icon space"></i>
            </div>
            <div
              class="col-lg-10 col-md-10 col-sm-10 col-10 pb-2"
              style="display: flex; align-items: center;"
            >
              <span v-if="post.space" class="post-space"
                >{{ post.space }} га</span
              >
              <span v-else class="post-space">Площадь не указана.</span>
            </div>
          </div>
          <div class="row">
            <div class="col-lg-1 col-md-1 col-sm-1 col-1 pb-2">
              <i class="icon category"></i>
            </div>
            <div
              class="col-lg-10 col-md-10 col-sm-10 col-10 pb-2"
              style="display: flex; align-items: center;"
            >
              <span v-if="post.categories" class="post-category">
                {{ toAbbr(post.categories.join(', ')) }}
              </span>
              <span v-else class="post-category"
                >Целевое назначение - отсутсвует.</span
              >
            </div>
          </div>
          <div v-if="post.date" class="row">
            <div class="col-lg-12 col-md-12 col-sm-12 col-12">
              <span class="post-date">Дата торгов: {{ post.date }}</span>
            </div>
          </div>
        </router-link>
      </div>
    </div>
    <div v-else class="notFound">
      <h3>
        Участков нету.
        <router-link v-if="isAdmin" to="/create-post" class="link">
          <br />Добавить участок
        </router-link>
      </h3>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data: () => ({
    filter: null,
  }),
  computed: {
    ...mapGetters(['posts', 'searchString', 'isAdmin']),
    displayPosts() {
      if (this.searchString) {
        return this.posts.filter(post => {
          if (!this.searchString) {
            return post
          }
          return Object.values(post)
            .join(' ')
            .toLowerCase()
            .includes(this.searchString.toLowerCase())
        })
      } else if (this.filter == 'hot') {
        return this.posts.filter(post => post.hot)
      } else return this.posts
    },
  },
  watch: {
    '$route.query': function() {
      const filterParam = this.$route.query.filters
      if (filterParam) {
        this.filter = filterParam
      }
    },
  },
  async mounted() {},
  methods: {
    toAbbr(string) {
      string = string.replace('Товарное сельское хозяйство', 'ТСХ')
      string = string.replace('Многоэтажное жилищное строительство', 'МЖС')
      string = string.replace('Индвивидуальное жилищное строительство', 'ИЖС')
      return string
    },
  },
}
</script>

<style lang="scss" scoped>
.link {
  color: #56739d !important;
}
.link:hover {
  color: #0e4dac !important;
  transition: 0s !important;
}
</style>
